import service from '@/utils/request';


/** 上次文件
 * 
 * 
 */
export default {



    // 获取待收货的 送货单
    uploapFile(file) {
        let formData =  new FormData();
        // 此时可以自行将文件上传至服务器
        if (file instanceof Array) {
          //判断是否是数组
          file.map((element) => {
            element.status = "uploading";
            element.message = "上传中...";
            formData.append("file", element.file);
          });
        } else {
          file.status = "uploading";
          file.message = "上传中...";
          formData.append("file", file.file);
        }
  
        return service.post(process.env.VUE_APP_API_FURL+process.env.VUE_APP_API_Fapi+'/admin/sys/files/upload/', formData);
    },

    
};



